p {
  padding: 0px;
  margin: 0px;
}
hr {
  border: none;
  border-top: 1px solid #333;
  color: #333;
  overflow: visible;
  text-align: center;
  height: 5px;
  width: 80%;
}

hr:after {
  background: #fff;
  content: "1 пересадка";
  padding: 0 4px;
  position: relative;
  top: -13px;
  width: 100%;
}

.flight {
  min-width: 480px;
  height: fit-content;
}

.choose_button {
  width: 100%;
  height: 30px;
  border: 1px solid black;
  background-color: #e26633;
  color: white;
  border-style: none;
  margin-bottom: 20px;
}

.showmore {
  display: inline-block;
  margin: 0 auto;
}
.flight_header {
  background-color: #3b81f3;
  display: flex;
  justify-content: space-between;
}
.flight_airlines {
  line-height: 35px;
  color: white;
}

.flight_totalPrice {
  text-align: right;
  color: white;
}
.flight_totalPrice::after {
  display: block;
  font-size: 10px;
  content: "Стоимость для одного взрослого пассажира";
}

.flight_body {
  display: flex;
  flex-direction: column;
  background: white;
}

.flight_leg {
  text-align: center;
  font-size: 14px;
  padding: 5px 10px;
}

.flight_date-time {
  text-align: center;
  padding-left: 10px;
  padding-right: 10px;
  display: flex;
  justify-content: space-between;
}

.flight_transfer {
  display: flex;
  justify-content: center;
  padding-top: 10px;
  padding-bottom: 10px;
  text-align: center;
}
.flight_carrier {
  font-size: 12px;
  padding: 3px 0px;
  border-bottom: solid rgb(8, 156, 248);
}

.flight_carrier_no {
  font-size: 12px;
  padding: 3px 0px;
}

.choose_button {
  cursor: pointer;
}

.showmore {
  margin-bottom: 40px;
}
